.navbar{
    height: 50px;
    border-bottom: 0.5px solid rgb(231, 228, 228);
    display: flex;
    align-items: center;
    font-size: 14px;
    color: gray;
    background-color: rgba(210, 210, 210, 0.324);
    border-radius: 10px;
    .search{
        display: flex;
        align-items: center;
        border: 0.5px solid lightgray;
        padding: 3px;
        input{
            border: none;
            outline: none;
            background-color: transparent;
            //search button place holder text
            &::placeholder{
                font-size: 12px;
            }
        }
    }
    
    .wrapper{
        width: 100%;
        display: flex;
        padding: 20px;
        //to place the item section right side
        align-items: center;
        justify-content: space-between;

        .items{
            display: flex;
            padding: 5px;

            .item{
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;
                
                .icon{
                    font-size:20px ;

                }
                .avatar{
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                }
                .counter{
                    width: 15px;
                    height: 15px;
                    background-color: red;
                    border-radius: 50%;
                    color: white;
                    display: flex;
                    justify-content: center;
                    font-size:10px ;
                    font-weight: bold;
                    position: absolute;
                    top: -4px;
                    right: -7px;
                }
            }
        }
    }
}